<template>
  <div class='userAnother'>
    <div class='myInfo'>{{$t('User.UserAnother')}}</div>
    <div class='infos'>
      <div class='avatar infoItem'>
        <el-avatar :size="100" :src="avatar" shape="square"></el-avatar>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.UserID')}}</span>
        <span class='infoRight'>{{userid}}</span>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.Nickname')}}</span>
        <span class='infoRight'>{{nickname}}</span>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.Email')}}</span>
        <span class='infoRight'>{{email}}</span>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.RealName')}}</span>
        <span class='infoRight'>{{realname}}</span>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.StudentID')}}</span>
        <span class='infoRight'>{{studentid}}</span>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.School')}}</span>
        <span class='infoRight'>{{school}}</span>
      </div>

      <div class='infoItem'>
        <span class='infoLeft'>{{$t('User.Auth')}}</span>
        <span class='infoRight'>{{userType}}</span>
      </div>
    </div>

    <div class='jumpToMe' @click='jumpToMe' v-if='myinfo'>
      <el-card>
        <div>{{$t('User.GotoYou')}}</div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import * as server from '../../../server.js'
import Cookies from 'js-cookie'

export default {
  name: 'user-info',
  data () {
    return {
      currentid: 0,
      userid: 0,
      avatar: '',
      nickname: '',
      email: '',
      realname: '',
      studentid: '',
      school: '',
      userType: '',
      myinfo: false
    }
  },
  methods: {
    getCurrentUser () {
      if (!Cookies.get('loginCookie')) {
        this.goLogin();
        return;
      }

      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'me',
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'USER_FOUND') {
            if (this.userid == response.data.userid) {
              this.myinfo = true;
            }
          }
          else {
            this.notfound();
          }
        }
      });
    },
    getUserInfo () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'other',
          'userid': this.userid,
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'USER_FOUND') {
            this.userid = response.data.userid;
            this.nickname = response.data.username;
            this.email = response.data.email;
            this.realname = response.data.realname;
            this.studentid = response.data.studentid;
            this.school = this.$t("User.UserSchool" + response.data.school);
            this.userType = this.formatUserType(response.data.userstatus, response.data.authname)
            this.avatar = this.getUserAvatar(this.userid)
          }
          else {
            this.notfound();
          }
        }
      });
    },
    jumpToMe () {
      this.$router.push({
        name: 'userMe'
      })
    }
  },
  mounted () {
    this.userid = this.$route.params.id;
    this.getUserInfo();
    this.getCurrentUser();
  }
}
</script>

<style scoped>
.userAnother {
  text-align: left;
  width: 90%;
  margin-left: 5%;
}
.myInfo {
  font-size: 25px;
  font-weight: bold;
}
.infos {
  margin-top: 30px;
  font-size: 20px;
}
.edit {
  float: right;
}
.infoRight {
  float: right;
}
.infoItem {
  margin-bottom: 20px;
}
.jumpToMe {
  text-align: center;
  margin-top: 60px;
  cursor: pointer;
  font-size: 20px;
}
</style>